.features-list {
  // border: 2px solid @green;
  // margin: 2px;
  // padding: 2px;

  text-align: left;
  margin: 0;
  padding: 0;
  padding-bottom: 37px;
  padding-top: 7px;
}

.features-list__item {
  position: relative;
  // border: 1px solid @black;
  // margin: 2px;
}

.features-list__header {
  margin: 0;
  padding-left: 69px;
  padding-top: 28px;
  color: @black;
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
}

.features-list__house {
  position: absolute;
  top: 18px;
  left: 22px;

  @media (min-width: @tablet-width) {
    top: 57px;
    margin: auto;
    left: 0;
    right: 0;
  }
}

.features-list__house-icon {
  height: 38px;
  width: 37px;

  @media (min-width: @tablet-width) {
    height: 78px;
    width: 75px;
  }
}

.features-list__present {
  position: absolute;
  top: 17px;
  left: 25px;

  @media (min-width: @tablet-width) {
    top: 57px;
    margin: auto;
    left: 0;
    right: 0;
  }
}

.features-list__present-icon {
  height: 38px;
  width: 32px;

  @media (min-width: @tablet-width) {
    height: 75px;
    width: 73px;
  }
}

.features-list__burger {
  position: absolute;
  top: 19px;
  left: 19px;

  @media (min-width: @tablet-width) {
    top: 59px;
    // left: 60px;
    margin: auto;
    left: 0;
    right: 0;
  }
}

.features-list__burger-icon {
  height: 43px;
  width: 38px;

  @media (min-width: @tablet-width) {
    height: 75px;
    width: 76px;
  }
}

