.features-list__description {
  color: @grey;
  margin: 0;
  padding-top: 14px;
  padding-left: 69px;
  padding-right: 20px;
  padding-bottom: 0;
}

@media (min-width: @tablet-width) {
  .features-list {
    display: flex;
    justify-content: space-between;
    padding: 0;
  }

  .features-list__item {
    padding: 30px;
    text-align: center;

    width: 33.3%;
    box-sizing: border-box;
    padding-bottom: 60px;

    // border: 2px solid @black;
    // margin: 2px;

    &:nth-child(1) {
      order: -1;
    }

    &:nth-child(2) {
      order: 2;
    }

    &:nth-child(3) {
      order: 1;
    }
  }

  .features-list__header {
    padding-left: 0;
    padding-top: 130px;
    font-size: 21px;
    line-height: 26px;
  }

  .features-list__description {
    padding-top: 20px;
    padding-left: 16px;
    padding-right: 16px;
    padding-bottom: 1px;
  }
}

@media (min-width: @desktop-width) {
  /*desktop*/
  .features-list__item {
    padding-bottom: 80px;
  }
}
