/*Colors*/
/*Sizes*/
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
}
body {
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  color: #000000;
  text-transform: uppercase;
  background-color: #f2f2f2;
}
a {
  text-decoration: none;
}
ul {
  list-style: none;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}
.site-container {
  width: 320px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .site-container {
    width: 768px;
  }
}
@media (min-width: 1200px) {
  .site-container {
    width: 1200px;
    box-shadow: 0 5px 15px 0 rgba(0, 1, 1, 0.2);
  }
}
/*Page header*/
.page-header {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  position: relative;
}
.page-header__logo {
  position: absolute;
  bottom: -27px;
  left: 50%;
  margin-left: -93px;
  height: 83px;
  z-index: 1;
}
.page-header__blue-background,
.page-header__bridge,
.page-header__clouds {
  display: none;
}
@media (min-width: 768px) {
  .page-header__logo {
    top: 0;
  }
  .page-header__clouds {
    display: inline;
  }
  .page-header {
    position: relative;
    justify-content: center;
  }
}
@media (min-width: 1200px) {
  .page-header__logo {
    height: 96px;
  }
  .page-header__bridge,
  .page-header__blue-background {
    display: inline;
  }
}
.main-nav {
  /*main-nav*/
  margin-bottom: 56px;
}
@media (max-width: 767px) {
  .main-nav--hide {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-nav--show {
    transform-origin: top;
    animation: main-nav__show-animation 0.3s;
  }
}
.main-nav__close-nav {
  position: absolute;
  top: 16px;
  right: 14px;
  border: none;
  background: none;
  fill: #ffffff;
  cursor: pointer;
}
.main-nav__open-nav {
  position: absolute;
  bottom: 14px;
  right: 14px;
  border: none;
  color: #81b3d2;
  background-color: #ffffff;
  fill: #81b3d2;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main-nav {
    margin-bottom: 0;
  }
  .main-nav__open-nav {
    display: none;
  }
  .main-nav__close-nav {
    display: none;
  }
}
@media (min-width: 1200px) {
  /*desktop*/
}
.site-list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
}
.site-list a {
  color: #ffffff;
}
.site-list__item {
  text-align: center;
  font-weight: bold;
  font-size: 18px;
  line-height: 40px;
  background-color: #81b3d2;
  width: 100%;
}
@media (max-width: 767px) {
  .site-list__item a {
    padding-top: 7px;
    padding-bottom: 7px;
    box-sizing: border-box;
    display: block;
    border-top: 1px solid #9ac2db;
    border-bottom: 1px solid #678fa8;
  }
  .site-list__item a:hover,
  .site-list__item a:focus {
    background-color: #669ec0;
  }
  .site-list__item a:active {
    background-color: #5496bd;
    color: rgba(255, 255, 255, 0.3);
  }
  .site-list__item:first-child a {
    border-top: 1px solid #81b3d2;
  }
  .site-list__item:last-child a {
    border-bottom: none;
    box-shadow: 0 -3px 3px 0 rgba(0, 1, 1, 0.2) inset;
  }
}
@media (min-width: 768px) {
  .site-list {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    padding-left: 6.3%;
    padding-right: 3%;
  }
  .site-list a {
    color: #000000;
  }
  .site-list a:hover,
  .site-list a:focus {
    color: #81b3d2;
  }
  .site-list a:active {
    color: rgba(0, 0, 0, 0.3);
  }
  .site-list__item--active a {
    color: #766357;
  }
  .site-list__item--active a:hover,
  .site-list__item--active a:focus {
    color: #604e43;
  }
  .site-list__item--active a:active {
    color: rgba(118, 99, 87, 0.3);
  }
  .site-list__item {
    font-size: 14px;
    line-height: 26px;
    background-color: #ffffff;
    width: 20%;
    padding-top: 15px;
    padding-bottom: 15px;
  }
  .site-list__item--left {
    text-align: left;
  }
  .site-list__item--right {
    text-align: right;
  }
  .site-list__item--left + .site-list__item--left {
    margin-left: -5px;
  }
  .site-list__item--right + .site-list__item--right {
    margin-left: 9px;
    margin-right: 3px;
  }
  .site-list__item--left + .site-list__item--right {
    margin-left: auto;
  }
}
@media (min-width: 1200px) {
  .site-list {
    padding-left: 7.6%;
    padding-right: 5.5%;
  }
  .site-list__item--left + .site-list__item--left {
    margin-left: 10px;
  }
  .site-list__item--right + .site-list__item--right {
    margin-left: 30px;
  }
}
