.reasons {
  text-align: center;
  margin: 0;
  padding: 0;
}
.reasons__item {
  position: relative;
  background-color: #81b3d2;
  color: #ffffff;
  margin: 0;
  padding-left: 25px;
  padding-right: 25px;
  padding-top: 39px;
  padding-bottom: 40px;
}
.reasons--other {
  padding-top: 26px;
  padding-bottom: 28px;
  background-color: #eeeeee;
  color: #000000;
}
.reasons__triangle-mobile {
  display: none;
}
@media (max-width: 767px) {
  .reasons--other:nth-child(2) {
    position: relative;
  }
  .reasons--other:nth-child(2)::before {
    position: absolute;
    left: 20px;
    top: -1px;
    content: "";
    height: 1px;
    width: 280px;
    border-top: 1px solid #d6d6d6;
    z-index: 1;
  }
  .reasons--other:nth-child(2)::after {
    position: absolute;
    left: 20px;
    bottom: -1px;
    content: "";
    height: 1px;
    width: 280px;
    border-bottom: 1px solid #d6d6d6;
    z-index: 1;
  }
  .reasons__triangle-mobile {
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
  }
}
.reasons--other .reasons__description,
.reasons--other .reasons__number {
  color: #333333;
}
.reasons__header {
  margin: 0;
  padding-bottom: 9px;
