  .reasons__item--with-triangle {
    position: relative;
  }
  .reasons__item--with-triangle::before {
    content: "";
    position: absolute;
    top: -1px;
    left: 0;
    height: 160px;
    width: 100%;
    background-image: url("../img/bg-triangle.svg");
    background-repeat: no-repeat;
    background-position: top;
