    left: 0;
    width: 125px;
    height: 1px;
    border-top: 1px solid #dbdbdb;
  }
  .emotions::after {
    position: absolute;
    content: "";
    top: 43px;
    right: 0;
    width: 125px;
    height: 1px;
    border-top: 1px solid #dbdbdb;
  }
  .emotions__caption {
    text-align: center;
    padding-top: 31px;
    padding-bottom: 56px;
  }
}
@media (min-width: 1200px) {
  .emotions::before {
    width: 280px;
  }
  .emotions::after {
    width: 280px;
  }
}
